import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import 'semantic-ui-css/semantic.min.css'
import { Button, Segment } from 'semantic-ui-react'
import Form from './Form'

class App extends Component {
  state = { era: [] }
  render() {
    return (
      <div>
        <Form />
      </div>
    );
  }
}

const eraCsvToJson = csv => {
  const jsonArray = []
  const csvArray = csv.replace(/\r/g, '').split('\n').map(str => str.split(','))
  const propNames = []
  for (let csvLineArray of csvArray) {
    const jsonData = {}
    const propName = csvLineArray.shift()
    jsonData[propName] = csvLineArray
    jsonArray.push(jsonData)
  }
  return jsonArray
}

const eraJsonToCsv = obj => {
  const csvArray = []
  for (const lineObj of obj) {
    const propName = Object.keys(lineObj)[0]
    const array = lineObj[propName]
    const csvLine = [propName, ...array].join(',')
    csvArray.push(csvLine)
  }
  return csvArray.join('\n')
}

export default App;



