import React, { Component } from 'react'
import {Button, Segment, Form, Input, Dropdown, Select, Label } from 'semantic-ui-react'

const arrayNames = ['能力','素質']
const array2Names = ['経験','Cstr']
const lineNames = ['能力','経験','素質','Cstr']

export default class extends Component {
  state = {
    名前: '',
    番号: '',
    呼び名: '',
    体力: '',
    気力: '',
    能力: [],
    経験: [],
    素質: [],
    Cstr: []
  }
  updateState = (label, value, index, index2) => {
    const state = JSON.parse(JSON.stringify(this.state))
    if (array2Names.includes(label)) {
      if (!Array.isArray(state[label][index])) state[label][index] = []
      state[label][index][index2] = value
    } else {
      state[label] = value
    }
    this.setState(state)
  }
  render() {
    const { state } = this
    return (
    <Form>
      <Form.Group>
        {['名前','番号','呼び名'].map(label =>
          <OneForm
            label={label}
            updateState={this.updateState}
            era={state}
            key={label}
          />
        )}
      </Form.Group>
      <Form.Group>
        {['体力','気力'].map(label =>
          <OneForm
            label={label}
            updateState={this.updateState}
            era={state}
            key={label}
          />
        )}
      </Form.Group>
      {lineNames.map(label => 
        <InputLine
          label={label}
          updateState={this.updateState}
          era={state}
          key={label}
        />
      )}
      <DownloadButton era={state} />
    </Form>
    )
  }
}

const OneForm = ({ label, index, updateState, era }) => {
  const handleChange = (e, data) => {
    let { value } = data
    updateState(label, data.value, index)
  }
  return (
    <Form.Field>
      <Input
        action={{ color: 'teal', content: label }}
        actionPosition='left'
        onChange={handleChange}
        placeholder={label}
      />
    </Form.Field>
  )
}

const InputLine = props => {
  let length = 1
  const { line, label, era } = props
  if (array2Names.includes(label)) {
    length = era[label].length + 1
  }
  return (
    <div>
      {[...Array(length)].map((e, index) =>
        <InputForm {...props} index={index} key={index} />
      )}
    </div>
  )
}

const InputForm = ({ label, index, updateState, era }) => {
  const options = require('./' + label)
  options.forEach(s => s.key = s.value)
  const handleChange = (e, data, index2) => {
    updateState(label, data.value, index, index2)
  }
  if (arrayNames.includes(label)) {
    return (
      <Label basic color='teal'>{label}
        <Dropdown multiple options={options} onChange={handleChange} />
      </Label>
    )
  } else if (array2Names.includes(label)) {
    return (
      <Form.Field inline >
        <Label basic color='teal' pointing='right'>{label + (index + 1)}</Label>
        <Input
          action={<Dropdown button basic floating options={options} onChange={(e, data) => handleChange(e, data, 0)} />}
          actionPosition='left'
          onChange={(e, data) => handleChange(e, data, 1)}
        />
      </Form.Field>
    )
  }
}

const DownloadButton = props => {
  const handleClick = () => {
    const era = JSON.parse(JSON.stringify(props.era))
    downloadArrayAsCsv(era, `Chara${( '000' + era['番号']).slice(-3)}.csv`)
  }
  return (
    <Form.Button inverted color='red' content='DL' onClick={handleClick} />
  )
}

const eraLineToCsv = (label, stat) => {
  switch (label) {
    case '体力': return [label, stat, 0].join(',')
    case '気力': return [label, stat, 1].join(',')
    case '能力':
    case '素質': return [label, stat, 1].join(',')
    case '経験':
    case 'Cstr': return [label, ...stat]
    default: return [label, stat].join(',')
  }
}

const eraJsonToCsv = era => {
  const csvArray = []
  for (const label in era) {
    const stat = era[label]
    if (Array.isArray(stat)) {
      for (const s of stat) {
        csvArray.push(eraLineToCsv(label, s))
      }
    } else {
      csvArray.push(eraLineToCsv(label, stat))
    }
  }
  return csvArray.join(',\r\n') + ','
}

function downloadArrayAsCsv(array, fileName) {
  var UTF_8_BOM = '%EF%BB%BF';
  const csv = eraJsonToCsv(array)
  var data = 'data:text/csv;charset=utf-8,' + UTF_8_BOM + encodeURIComponent(csv);
  var element = document.createElement('a');
  element.href = data;
  element.setAttribute('download', fileName);
  document.body.appendChild(element);
  element.click();
  document.body.removeChild(element);
}


